/*effect*/
.food-image .overlay, .event-image .overlay, .staff-image .overlay {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    left: 0;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    -webkit-transition: all 0.4s ease-in-out 0s;
    transition: all 0.4s ease-in-out 0s;
    width: 100%;
}
.food-image, .event-image, .staff-image {
    cursor: default;
    overflow: hidden;
    position: relative;
    text-align: center;
}
.food-image:hover .overlay, .event-image:hover .overlay, .staff-image:hover .overlay {
    opacity: 1;
}
.food-image a.info, .event-image a.info, .staff-image a.info {
    color: #fff;
    display: flex;
    flex-direction: column;
    font-size: 22px;
    font-weight: 600;
    height: 100%;
    justify-content: center;
    letter-spacing: 5px;
    opacity: 1;
    position: absolute;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all 0.2s ease-in-out 0s;
    transition: all 0.2s ease-in-out 0s;
    width: 100%;
