.modal-container {
margin:0px;
display: flex;
align-items: center;
justify-content: center;
position: fixed;
top: 0;
left: 0;
width: 100vw;
height: 100vh;
overflow: hidden;
background: rgba(0,0,0,0.45);
z-index: 99999999;
}

.close-modal{
    color: red;
    cursor : pointer; 
}

.close-modal-button{
    border-radius: 50%;
    position: absolute;
    top: 5%;
    left: 95%;
}

.banner-responsive{
    width: auto;
    height: 390px;
}
.single-blog-image{
    display: flex;
    justify-content: center;
}

    @media screen and (max-width: 624px) {
    .modal-area {
        background-color: white;
        width: 620px;
        height: 100%;
        padding: 15px;
        overflow: scroll;
        overflow-x : hidden;
    }
    .banner-responsive{
        width: 100%;
        height: 50%;
    }
    }
    @media screen and (min-width: 625px) {
    .modal-area {
        margin: 15%;
        background-color: white;
        width: 100%;
        height: 90%;
        border-radius: 10px;
        padding: 15px;
        overflow: scroll;
        overflow-x : hidden;
    }
}
.img-carousel{
    width:750px;
    height:450px;
}
#gites{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.heading h1:after {
    content: '';
    display: block;
    width: 8px;
    height: 2px;
/*     background: #BABABA;
 */    margin-top: 4px;
    
}

.clear-fix {
    clear: both;
}

.hidden {
    visibility: hidden !important;
}

/* end global */

/* start of our services section */

#our-services {
/*     background: #f6f6f6;
 */    min-height: 100vh;
    padding-top: 25vh;
}

/* OUR SEERVICES LEFT SIDE */

#our-services .desc {
    width: 33%;
    float: left;
}

#our-services .desc .heading h1 {
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1.4px;
    margin-bottom: 30px;
}

#our-services .desc .text {
    color: #BABABA;
}

/* OUR SERVICES RIGHT SIDE */

#our-services .services {
    width: 67%;
    float: left;
}

#our-services .services .column {
    float: left;
    margin-right: 60px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    transition: -webkit-transform 0.3s ease;
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

#our-services .services .column .service {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 120px;
    /*background: #FFFFFF*/
    -webkit-transition: all 0.1s ease;
    transition: all 0.1s ease;
    margin-bottom: 7px;
}

#our-services .services .column .service:hover {
    background: #28282e;
    color: #fff;
}

#our-services .services .column .service:hover .service-icon {
    color: #fff;
}

#our-services .services .column .service .service-desc {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    text-align: center;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: bold;
    transition: -webkit-transform 0.3s ease;
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

#our-services .services .column .service .service-desc .service-icon  {
    font-size: 20px;
    margin-bottom: 5px;
    color: #f57469;
}

/* end of our services section */

/* media query */

@media all and (max-width: 967px) {
    #our-services {
        padding-top: 60px;
    }
    
    #our-services .desc {
        width: 100%;
        float: none;
        margin-bottom: 30px;
    }
    
    #our-services .services {
        width: 100%;
    }
    
    #our-services .services .column {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        margin-right: 10px;
    }
    
    #our-services .services .column .service .service-desc {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}
